import React from "react";
import { NumericFormat } from "react-number-format";

interface DeltaBarComponentProps {
  value: number;
  delta: number;
}

export const DeltaBarComponent = (props: DeltaBarComponentProps) => {
  const { value, delta } = props;

  return (
    <div>
      <span className="text-sm font-small text-gray-900">
          {value}
        </span>
       <span className="text-sm font-small text-gray-900 float-right">
          <NumericFormat
            value={delta}
            prefix={"$"}
            displayType="text"
            allowNegative
            allowLeadingZeros
            thousandSeparator=","
          />
        </span>
    </div>
  );
};
